.mainItem{
    display: flex;
    gap: 10px;
}
.avatar{
    margin-bottom: 2px;
}
.defaultAvatar{
    margin-bottom: 2px;
    background-color: #9bcfe0;
    padding: 20px;
    border-radius: 50%; 
}
.avatarInfo{
    display: flex; 
    margin-bottom: 5px;
    flex-direction: column; 
    margin-left: 10px;
    align-items: center;
    & .followButton{
        width: 90px;
        font-size: 16px;
    }
}
.city{
    flex-direction: column;
}
.userInfo{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 16px;
}
.container{
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.status{
    width: 300px;
    color: rebeccapurple;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    margin: 0;
}
p{
    margin: 0;
}
