@mixin flexContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}

.chosing{
    font-size: 24px;
    cursor: pointer;
    font-weight: 900;
}
.pageContainer{
    list-style: none;
    width: 100%;
    @include flexContainer;
    box-sizing: border-box
}
.stringNumbers{
    @include flexContainer;
    flex-direction: row;
    gap:10px;
}
.usualNumberPage{
    font-size: 24px;
    cursor: pointer;
}
.center{
    position: absolute;
    top:50%;
    left: 50%;
}